import React from 'react';
import {Option, OptionVariant} from '@here/hds-select';

// eslint-disable-next-line no-new
new Option();

interface HDSOptionProps extends React.HTMLAttributes<HTMLDivElement> {
  selected?: boolean;
  disabled?: boolean;
  variant?: OptionVariant;
}
const HDSOption = React.forwardRef(
  ({selected, disabled, variant, children, className, ...rest}: HDSOptionProps, ref) => (
    <hds-option
      {...(selected && {selected})}
      {...(disabled && {disabled})}
      variant={variant}
      {...(className && {class: className})}
      ref={ref}
      {...rest}
    >
      {children}
    </hds-option>
  )
);

export default HDSOption;
